import { css } from "lit-element";

const sharedStyles = css`
    :host{
        --red:   #C62828;
        --gray0: #FFFFFF;
        --gray1: #dbe1e8;
        --gray2: #b2becd;
        --gray3: #6c7983;
        --gray4: hsl(0, 0%, 50%);
        --gray5: hsl(0, 0%, 15%);
        --gray6: hsl(0, 0%, 7%);
        --gray7: hsl(0, 0%, 0%);
        --blue:  #0084a5;
    }
    .light{
        --bg: var(--gray0);
        --card-bg: var(--gray0);
        --accent: var(--blue);
        --text: var(--gray7);
        --label: var(--gray3);
    }
    .dark{
        --bg: var(--gray6);
        --card-bg: var(--gray5);
        --accent: var(--red);
        --text: var(--gray0);
        --label: var(--gray0);
    }
`
export default sharedStyles